import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';

import { AccountService } from '../account.service';
import { environment } from '../../environments/environment';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  isUserAuthenticated = false;
  userName: string;

  constructor(private httpClient: HttpClient, private accountService: AccountService) { }

  ngOnInit() {
    this.accountService.isUserAuthenticated.subscribe(isAuthenticated => {
      this.isUserAuthenticated = isAuthenticated;
      if (this.isUserAuthenticated) {
        this.httpClient.get(`${environment.apiUrl}/home/name`,{responseType: 'text', withCredentials: true}).subscribe(theName => {
          this.userName = theName;
        });
      }
    });
  }

  logout() {
    this.accountService.logout();
  }

  simulateFailedCall() {
    this.httpClient.get(`${environment.apiUrl}/home/fail`).subscribe(result => {
      console.log(result);
    }, (error: HttpErrorResponse) => {
      console.warn(error);
    });
  }

  login() {
    window.location.href="https://www.blinkingcaret.com";
    //this.accountService.login();
  }

}
